:root{
  --primary-color:#E70F0F;
}

*{
  box-sizing:border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing:antialiased;
  outline:none !important;
}

body{
  margin:0;
  padding:0;
  direction:rtl;
  font-family: yekanbakh,sans-serif;
}

.App{
  width:100vw;
  height:auto;
  min-height:100vh;
  background:url('assets/images/bg.jpg') no-repeat center center;
  background-size:cover;
  position:relative;
  color:#FFF;
  padding:2rem;
  z-index:1;
  background-attachment: fixed;
}

.app-inner{
  position:relative;
  z-index:2;
}

.App::before{
  width:100vw;
  height:100vh;
  background:-webkit-linear-gradient(90deg,rgba(0,0,0,1),rgba(0,0,0,.6));
  content:'';
  display:block;
  position:fixed;
  bottom:0;
  left:0;
  right:0;
}

.app-header{
  width:100%;
  height:auto;
  display:flex;
  align-items:center;
  justify-content:flex-start;
  direction:ltr;
}

.app-header strong{
  color:var(--primary-color);
  font-size:3rem;
  margin:0;
  font-weight:900;
}

.app-header p{
  font-weight:200;
  position:relative;
  top:-15px;
}

h1,h2,h3,h4,h5,h6{
  margin:0;
  padding:0;
}

.app-center{
  width:100%;
  height:auto;
  display:flex;
  align-items:center;
  justify-content:center;
  flex-direction: column;
  width:750px;
  margin:auto;
  max-width:100%;
  text-align:center;
}

input{
  width:500px;
  max-width:80vw;
  border-radius:14px;
  background:#FFF;
  direction:ltr;
  color:#2d2d2d;
  display:block;
  flex: 0 0 auto;
  padding:1rem;
  border:none;

  &::placeholder{
    text-align:right;
    color:#8d8d8d;
    direction:rtl;
  }
}

.btn-primary{
  background:var(--primary-color) !important;
  color:#FFF !important;
  border-radius:14px;
  font-weight:800;
  border:none !important;
  padding:1rem 2rem;
  margin:2rem;
}

h1{
  font-weight:800;
  font-size:3rem;
}

.file-input-container{
  position:relative;

  *:not(input){
    position:relative;
    z-index:1;
  }

  input{
    position:absolute;
    opacity:0;
    z-index:2;
    height:100%;
    -webkit-appearance: none;
    appearance: none;
  }
}

.card{
  background:rgba(0,0,0,0.1) !important;
  backdrop-filter:blur(30px) saturate(180%);
  -webkit-backdrop-filter:blur(30px) saturate(180%);
  border:1px solid rgba(255,255,255,.3);
}

.app-center video{
  width:100%;
  height:auto;
  background:#2d2d2d;
  border-radius:16px;
}

.prev-video{
  border-bottom:1px solid rgba(255,255,255,.3);
  padding:.75rem 0;

  &>strong{
    max-width:80%;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
}

.cursor-pointer{
  cursor: pointer;
}